import React , { Component}from 'react';


class TodoApp extends Component{

   state = {
       input : "",
       items : []
   }


    handleChange = (event)=>{
     
      this.setState({
        
        input:event.target.value

      })
      
     
    }


    storeItems = (event)=>{

        event.preventDefault();
        const{input}=this.state
      
        
        this.setState({
            items:[...this.state.items,input],
            input:""
           
        })


    }

    deleteItem = (key)=>{
    
        const allitems = this.state.items

         allitems.splice(key,1)

        this.setState({
            items:allitems
        })

       
    }

    render(){

        const {input,items} = this.state;

        
        return(
        

            <form className='todo-container' onSubmit={this.storeItems}>
               

               <div className='input-section'>
               <h1>TodoApp</h1>
                   <input type="text" value={input} onChange={this.handleChange}  placeholder='Enter items'/>
                    
               </div>

               <ul>{items.map((data,index)=>{
                   return <li key={index}>{data} <i className='fas fa-trash-alt' onClick={()=>{this.deleteItem(index)}}></i></li>
               })}</ul>
            </form>
        )
    }
}

export default TodoApp;